import * as React from 'react'; import PropTypes from 'prop-types'; import Toolbar from '@mui/material/Toolbar'; import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton'; import SearchIcon from '@mui/icons-material/Search'; import Typography from '@mui/material/Typography'; import Link from '@mui/material/Link';

//handler onClick={(e) => handleClickAbout(e, "About")} const handleClickAbout = (event, text) => { switch(text) {case "About":console.log(event.target);  console.log(text);break;}};
function Header(props) {const { sections, sztitle, szlink } = props;

  return (<React.Fragment><Toolbar sx={{ borderBottom: 1, borderColor: 'divider' }}><Button variant="outlined" size="small" href="about" >About Us</Button>
        <Typography component="h2" variant="h5" color="inherit" align="center" noWrap sx={{ flex: 1 }} >{sztitle}</Typography>
        <IconButton><SearchIcon />        </IconButton>      <Button variant="outlined" size="small"> Sign up </Button>   </Toolbar>
      <Toolbar component="nav" variant="dense"  sx={{ justifyContent: 'space-between', overflowX: 'auto' }}  >
        {sections.map((section) => (<Link color="inherit" noWrap key={section.titlesec} variant="body2" href={section.urlsec} target={szlink} sx={{ p: 1, flexShrink: 0 }} > {section.titlesec} </Link>   ))}
      </Toolbar></React.Fragment>
  );
}

Header.propTypes = {sections: PropTypes.arrayOf(PropTypes.shape({ titlesec: PropTypes.string.isRequired, urlsec: PropTypes.string.isRequired, }),  ).isRequired,
  sztitle: PropTypes.string.isRequired,
};

export default Header;
