const szsections = [{ titlesec: 'What is a repair caf?', urlsec: 'what-is-a-repair-cafe' },  { titlesec: 'Lordship Rec Hub', urlsec: '#' },  { titlesec: 'Broadwater Farm', urlsec: '#' }, { titlesec: 'Find a Repair shop (not free)', urlsec: 'https://therestartproject.org/repairdirectory/' }, 
 ];

const mainFeaturedPostContent = { title: 'Book a free session', description:  "Learn how to repair your stuff with our coaches and volunteers.",
  image: 'https://source.unsplash.com/random',  imageText: 'book here',  linkText: 'Click to reserve a slot', szlinktarg: "_blank"};

const szfeaturedPosts = [ 
{date: '1st Sunday of the month', title:'Lordship Rec Hub',  description:  '4-7pm in the Community Room.',
    image: 'https://source.unsplash.com/random',   imageLabel: 'Lordship Rec Hub', },
{date: '3rd Tuesday of the month', title:'Broadwater Farm',  description:  '1:30-4:30pm at the Community Centre.',
    image: 'https://source.unsplash.com/random',   imageLabel: 'Broadwater Farm Community Centre', },];

export default function App() { return (<ThemeProvider theme={theme}><CssBaseline />
      <Container maxWidth="lg"><Header sztitle='Haringey Fixers' sections={szsections} szlink="_blank" />
        <main><MainFeaturedPost post={mainFeaturedPostContent} /><Grid container spacing={4}> {szfeaturedPosts.map((post) => (<FeaturedPost key={post.title} post={post} /> ))} </Grid>
          <Grid container spacing={5} sx={{ mt: 3 }}>{/* <Main title="From the firehose" posts={posts} /> */}
              {/* <Sidebar title={sidebar.title} description={sidebar.description} archives={sidebar.archives} social={sidebar.social} /> */}
            </Grid></main></Container>
      <Footer title="" description="Made by Haringey Fixers" /></ThemeProvider>  );}
